export default {
  methods: {
    loginConfirm () {
      if (!this.$store.getters.token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '需要登陆之后才能操作哦',
          confirmButtonText: '去登陆',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // on confirm
            this.$router.replace({
            // 此处replace代替push,区别在是否添加历史，replace代表将URL进行替换，所以回退时可以直接跳转到之前的页面，而push会一直叠加历史
              path: '/login',
              query: {
                backurl: this.$route.fullPath // 注意此处是$route
              } // 此处query是包含查询参数--想要回跳页面backurl的跳转
            })
            return true
          })
          .catch(() => {
            // on cancel
          })
        return true
      }
      return false
    }
  }
}
